@tailwind base;
@tailwind components;
@tailwind utilities;

@layer utilities {
  @variants responsive {
    .animation-delay-2000 {
      animation-delay: 2s;
    }

    .animation-delay-4000 {
      animation-delay: 4s
    }

    .text-shadow {
      text-shadow: 2px 2px 4px black;
    }

    .text-shadow-md {
      text-shadow: 4px 4px 8px black;
    }

    .text-shadow-lg {
      text-shadow: 15px 15px 30px black;
    }

    .text-shadow-none {
      text-shadow: none;
    }
  }
}